.detailProductContent{
    display: flex;
    align-items: center;

    .backBtn {
        opacity: 0.5;
        transition: opacity 0.2s ease-in-out;
        cursor: pointer;
        margin-right: 21px;
        &:hover {opacity: 1}
      }
  }
  .titleContent{ 
    font-size: 2em;
    margin: 0;
    padding: 0;
}

.detailPageWrapper{
    display: flex;
    align-items: flex-start;
    flex-wrap: nowrap;
    margin-top: 40px;

    .imgProductBox{
        width: 50%;
        img{
            width: 100%;
            padding: 20%;
            border-radius: 20px;
        }
    }

    .infoProductBox{
        width: 50%;
        padding: 30px;
        .deliveryInfo{
            margin: 30px auto;
            font-size: 20px;
            color: #444;
            span{
                color: #000;
                font-weight: 600;
            }
        }
        .productPrice {
            font-size: 35px;
            margin-bottom: 30px;
        }
        .controlOrderBox{
            display: flex;
            align-items: center;
            justify-content: start;
        }
        .controlOrderBox {
            button{
                width: 250px;
                margin-right: 30px;
            }
        }
        .colorTitle{
            font-size: 20px;
            color: #444;
            margin-bottom: 10px;
        }
        .colorBox{
            display: flex;
            align-items: center;
            justify-content: start;

            .colorBoxItem{
                cursor: pointer;
                margin-right: 10px;
                width: 50px;
                height: 50px;
                border: solid #00000030 0;
                border-radius: 50%;
                &:nth-child(1) { background-color: rgb(202, 202, 202);}
                &:nth-child(2) { background-color: rgb(78, 78, 78);}
                &:nth-child(3) { background-color: rgb(233, 132, 132);}
                -webkit-transition: transform 0.2s ease-in-out, border 0.2s ease-in-out;
                -moz-transition: transform 0.2s ease-in-out, border 0.2s ease-in-out;
                -o-transition: transform 0.2s ease-in-out, border 0.2s ease-in-out;
                -ms-transition: transform 0.2s ease-in-out, border 0.2s ease-in-out;
                transition: transform 0.2s ease-in-out, border 0.2s ease-in-out;               
                &:hover{
                    -webkit-transform: scale(1.2);
                    -moz-transform: scale(1.2);
                    -o-transform: scale(1.2);
                    -ms-transform: scale(1.2);
                    transform: scale(1.2);                 
                }

            }
            .activeColor {
                border: solid #00000030 10px;
            }
        }

    }


    .addBox{
        position: relative;
        overflow: hidden;
        width: 55px;
        height: 55px;
        border-radius: 7px;
        cursor: pointer;
      }
}


@media screen and (max-width:760px) {
    .titleContent { 
        font-size: 16px;
    }
    .detailPageWrapper{
        flex-direction: column;
        margin-top: 0;
        .imgProductBox, .infoProductBox{
            width: 100%;
        }
        .imgProductBox{
           img {padding: 0;}
        }

        .infoProductBox{
            .controlOrderBox {
                button{
                    width: 200px;
                    margin-right: 20px;
                    font-size: 14px;
                    img{
                        right: 20px;
                    }
                }
            }

            padding: 20px;
            .productPrice{
                font-size: 25px;
            }
            .colorTitle, .deliveryInfo{
                font-size: 14px;
            }
        }



    }


  }